<script>
import Section from "@/components/Wsy_section.vue";
import WsyLogin from "@/components/wsy_login";
import BetterScroll from "better-scroll";
export default {
  name: "wsy_index",
  components: {
    Section,
    WsyLogin,
  },
  props: {},
  data() {
    return {
      // better-scroll实例
      bs: null,
      index: 1,
      //   导航区域数据
      wsy_nav: [
        {
          title: "特色课",
          path: "/lwh_course",
          img:
            "data:image/png;base64,",
        },
        {
          title: "一对一辅导",
          path: "/oto",
          img:
            "data:image/png;base64,",
        },
        {
          title: "学习日历",
          path: "/StudyCalendar",
          img:
            "data:image/png;base64,",
        },
      ],
      // 名师阵容区域数据
      famousTeacher: {},
      // 精品课程数据
      excellentCourse: {},
      // 推荐课程数据
      recommendedCourse: {},
      // 明星讲师
      starLecturer: {},
      showData: [],
    };
  },
  created() {},
  mounted() {
    this.$nextTick(() => {
      setTimeout(() => {
        this.bs = new BetterScroll(this.$refs.scrollBox, {
          probeType: 3,
          click: true,
        });
      }, 800);

      this.getBanner();
      this.getIndex();
    });
    let token = sessionStorage.getItem("token");
    if (token) {
      this.$store.commit("wsy_Close", false);
    }
  },
  activated() {},
  update() {},
  beforeRouteUpdate(to, from, next) {
    console.log(to, from);

    // if(from.path == "/lwh_main" && to.path == "/StudyCalendar"){
    //     this.$store.commit("wsy_Close",true)
    //     next(false)
    // }
    next();
  },
  methods: {
    //   点击进入课程详情页面
    ToCourseDetail(info) {
      console.log(info);
      window.localStorage.setItem("CourseInfo", JSON.stringify(info));
      this.$router.push("/yz_courseDetail");
    },
    // 获取轮播图数据
    async getBanner() {
      let data = await this.$http.get("/api/app/banner");
      // console.log(data);
    },
    // 获取首页列表数据
    async getIndex() {
      let { data } = await this.$http.get("/api/app/recommend/appIndex");
      this.showData = data.data;
      console.log(this.showData);
      this.famousTeacher = data.data[4];
      this.excellentCourse = data.data[1];
      this.recommendedCourse = data.data[3];
      this.starLecturer = data.data[0];
    },
    // 点击名师阵容跳转到讲师详情
    toTeacherInfo(id) {
      console.log(id);
      let token = sessionStorage.getItem("token");
      if (token) {
        this.$router.push({
          path: "/teacher",
          query: { id: id },
        });
      } else {
        this.$store.commit("wsy_Close", true);
      }
    },
  },
  filters: {
    periods(val) {
      switch (val) {
        case 0:
          return "零";
          break;
        case 1:
          return "一";
          break;
        case 2:
          return "二";
          break;
        case 3:
          return "三";
          break;
        case 4:
          return "四";
          break;
        case 5:
          return "五";
          break;
        case 6:
          return "六";
      }
    },
  },
  computed: {},
  watch: {},
};
</script>

