  flex-direction: column;
  //   overflow: hidden;
  > .wsy_scroll {
    flex: 1;
    background: #f0f2f5;
    overflow: hidden;
  }
}
.scrollBox {
  width: 100%;
  min-height: 101%;
}
// vant轮播图组件样式
.wsy_banner {
  width: 100%;
  height: 2rem;
}
.wsy-swipe .van-swipe-item {
  width: 7.5rem;
  height: 2rem;
  font-size: 20px;
  line-height: 2rem;
  text-align: center;
  background-color: #39a9ed;
  > img {
    width: 100%;
    height: 100%;
  }
}
.wsy_kinds {
  width: 100%;
  height: 1.2rem;
  padding: 0 0.2rem;
  box-sizing: border-box;
  padding-bottom: 0.2rem;
  position: relative;
  > ul {
    position: absolute;
    top: -0.3rem;
    left: 0;
    right: 0;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0 0.1rem;
    > li {
      border-radius: 0.1rem;
      width: 30%;
      height: 100%;
      background: #fff;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      > img {
        width: 20%;
        height: 20%;
      }
      > p {
        font-size: 0.12rem;
        margin-bottom: 0;
        color: #8c8c8c;
      }
    }
  }
}
</style>
